import logo from './logo.svg';
import './App.css';
import { Autocomplete, Icon, InputAdornment, TextField, Typography } from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import AccountCircle from '@mui/icons-material/AccountCircle';
import { useState } from 'react';
import React from 'react';
import { createFilterOptions } from '@mui/material/Autocomplete';

var opcionesPrueba=["ABA","ADDA","POP"]
var otrasOpcionesPrueba=["CACA","AQQQ","LGN","YOLO"]

function Buscador(){
  const [open, setOpen] = React.useState(false);
  const [options, setOptions] = React.useState(opcionesPrueba);
  const loading = open && options.length === 0;

  const filterOptions = createFilterOptions({
    ignoreAccents: true,
    ignoreCase: true,
    limit: 20
  });

  React.useEffect(() => {
    let active = true;

    if (!loading) {
      return undefined;
    }

    (async () => {
      await fetch("./tesissim/titulos.json").then(resp=>resp.json()).then((json)=>opcionesPrueba=json);
      await sleep(1e3); // For demo purposes.

      if (active) {
        setOptions([...opcionesPrueba.slice(0,500)]);
      }
    })();

    return () => {
      active = false;
    };
  }, [loading]);

  React.useEffect(() => {
    if (!open) {
      setOptions([]);
    }
  }, [open]);
  return(
    <div>
      <TextField 
        label="Ingrese su bsqueda"
        InputProps={{endAdornment:(<InputAdornment position='start'><SearchIcon></SearchIcon></InputAdornment>),}}
        variant="standard"
        sx={{ input: { color: 'white'},'& label.Mui-focused': {
          color: '#A0AAB4',
        },
        '& .MuiInput-underline:after': {
          borderBottomColor: '#B2BAC2',
        },
        '& .MuiOutlinedInput-root': {
          '& fieldset': {
            borderColor: '#E0E3E7',
          },
          '&:hover fieldset': {
            borderColor: '#B2BAC2',
          },
          '&.Mui-focused fieldset': {
            borderColor: '#6F7E8C',
          },
        }, }}>
      </TextField>
      <Autocomplete 
        freeSolo 
        open={open}
        onOpen={() => {
          setOpen(true);
        }}
        onClose={() => {
          setOpen(false);
        }}
        options={options}
        loading={loading}
        //options={opcionesPrueba.map((option) => option)}
        renderInput={(params) => <TextField {...params} label="Seleccione una tesis" />}
        filterOptions={filterOptions}
        //filterOptions={(x) => x}
      ></Autocomplete>
    </div>
  )
}



function App() {
  fetch("http://localhost:3000/tesissim/titulos.json").then(resp=>resp.json()).then((json)=>opcionesPrueba=json);
  return (
    <div className="App">
      <header className="App-header">
        <Typography variant='h1'>PumaTesis</Typography>
        <Typography variant='subtitle1'>El buscador de tesis ms avanzado del mundo</Typography>
        <Buscador/>
      </header>
      
    </div>
  );
}

function sleep(delay = 0) {
  return new Promise((resolve) => {
    setTimeout(resolve, delay);
  });
}


export default App;
